@font-face {
  font-family: "Avenir";
  src: url("./fonts/Avenir.ttc") format("truetype");
}

p {
  font-family: "Avenir", Helvetica, sans-serif;
  color: #fff;
}

.App {
  text-align: center;
}

.carousel {
  width: 100% !important;
  height: 700px !important;
  object-fit: cover;
  position: relative;
}

.logo {
  height: 30px;
  width: 30px;
}

.show-container {
  display: inline-block;
  justify-content: space-evenly;
}

.banner {
  position: absolute;
  width: 70%;
  top: 30%;
  left: 50%;
  color: white;
  background-color: rgba(49, 57, 76, 0.9);
  margin-left: -35%;
}

.header-button {
  position: absolute;
  top: 140%;
  margin-left: -5%;
  font-size: x-large;
}

.nav-bar {
  background-color: #d9d9d9;
}

.nav-item {
  color: #31394c !important;
  font-family: "Avenir";
  letter-spacing: 0.035em;
  font-weight: 600;
}

.home-subtitle {
  font-size: 12px;
}

.home-text {
  font-family: "Arvo";
  color: #9c250f;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 18pt;
  display: inline;
  cursor: pointer;
}

h1 {
  margin: 30px;
  font-family: "Arvo";
  font-style: normal;
  font-weight: 700;
  font-size: 64px;
}

h2 {
  margin: 15px;
  font-family: "Arvo";
  font-style: normal;
  font-weight: 500;
  font-size: 36pt;
  color: #d9d9d9;
}

h3 {
  margin: 10px;
  font-family: "Arvo";
  font-style: normal;
  font-weight: 300;
  font-size: 35px;
  color: #d9d9d9;
}

.navbar-expand-md .navbar-nav .nav-link {
  padding-right: 1.5rem;
  padding-left: 1.5rem;
}

.ticket-button {
  background-color: white;
  color: #181e2c;
  z-index: -1;
  position: absolute;
}

.upcoming-shows {
  padding: 50px 150px;
}

.card {
  margin: 20px;
  background-size: cover;
  border: 0px;
  border-radius: 30px;
}

.card-title {
  height: 60px;
  padding-top: 10px;
  text-align: left;
  font-weight: bold;
  color: #181e2c;
  overflow: hidden;
}

.card-price {
  text-align: left;
  margin: 0;
  color: #181e2c;
  padding: 10px 0;
}

.card-date {
  text-align: left;
  padding-bottom: 10px;
  font-style: italic;
  color: #181e2c;
}

.card-image {
  border-radius: 30px 30px 0 0;
  height: 250px;
  object-fit: cover;
}

.primary-btn {
  margin-bottom: 10px;
}

body {
  background: radial-gradient(
    157.7% 179.99% at 31.6% 2.99%,
    #3c1610 0%,
    #9c250f 0.01%,
    #3c1610 100%
  );
  font-family: "Avenir";
  letter-spacing: 0.035em;
}

.btn-primary:hover,
.btn-primary:focus,
.btn-primary:active,
.btn-primary.active,
.open > .dropdown-toggle.btn-primary {
  color: #fff;
  background-color: #181e2c;
  border-color: #fff;
  /* border-color: #285e8e; */
  /*set the color you want here*/
}

.btn-primary {
  color: #fff;
  background-color: #31394c;
  border: none;
}

.btn-secondary:hover,
.btn-secondary:focus,
.btn-secondary:active,
.btn-secondary.active,
.open > .dropdown-toggle.btn-primary {
  color: black;
  background-color: #acabab;
  border-color: #285e8e;
  /*set the color you want here*/
}

.btn-secondary {
  color: black;
  background-color: #d9d9d9;
  border: none;
}

.shows-page {
  padding-top: 50px;
}

.about-container {
  padding-top: 150px;
  padding-bottom: 50px;
}

.about {
  color: white;
}

.about-image {
  height: 500px;
  width: 500px;
  object-fit: cover;
}

.about-text {
  padding-left: 25px;
  padding-right: 25px;
}

.about-row {
  padding-top: 50px;
  padding-bottom: 50px;
}

.about-col {
  margin: auto;
  padding-top: 50px;
  padding-bottom: 50px;
}

.history-text {
  color: #BFC7D0;
}

.first-show {
  padding-top: 25px;
  padding-bottom: 25px;
  margin: auto;
}

.faq-card {
  height: 175px;
}

.faq-container {
  padding-top: 50px;
}

.faq-hero-image {
  width: 100% !important;
  height: 500px !important;
  object-fit: cover;
  position: relative;
}

.faq-banner {
  position: absolute;
  width: 30%;
  top: 50%;
  left: 50%;
  color: white;
  background-color: rgba(49, 57, 76, 0.9);
  margin-left: -15%;
  margin-top: -200px;
}

.business-container {
  margin: 50px;
  background-color: #31394c;
  padding: 5%;
}

.business-image {
  display: block;
  height: 200px;
  width: 200px;
  border-radius: 50%;
  margin: 0 auto;
  margin-top: 30px;
}

.business-name {
  padding: 5px;
  font-size: 18pt;
  height: 20px;
  text-decoration: underline;
  color: white;
}

.footer {
  background-color: #d9d9d9;
  height: 100%;
}

.footer-logo {
  height: 150px;
  width: 150px;
  border-radius: 50%;
}

.footer-address {
  color: #484444;
}

.social {
  height: 20px;
  width: 20px;
}

.footer {
  background-color: white;
  align-items: center;
}

.footer-logo {
  height: 100px;
  width: 100px;
  border-radius: 50%;
}

.footer-address {
  color: #484444;
  margin: auto;
}

.footer-row {
  padding: 20px;
  display: flex;
  align-items: center;
}

.social {
  height: 30px;
  width: 30px;
  margin: 10px;
  object-fit: contain;
}

.breadcrumb {
  padding-top: 60px;
  color: white !important;

  
}

.white-body {
  background-color: #fff;
  padding-top: 100px;
}

.breadcrumb-food {
  color:#181e2c;
  text-align: left;
}
.concession-hero-image {
  padding-top: 50px;
  padding-bottom: 50px;
  width: 75% !important;
  height: 250px !important;
  object-fit: cover;
  /* position: relative; */
}

.refreshments {
  color: black;
}

.concessions-banner {
  position: absolute;
  width: 70%;
  top: 35%;
  left: 50%;
  color: white;
  background-color: rgba(49, 57, 76, 0.9);
  margin-left: -35%;
}

.concession-container {
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left: 25px;
  padding-right: 25px;
  border-radius: 30px;
  margin: 20px;
}

.concession-heading {
  color: #181e2c;
}

.concession-text {
  color: #646b73;
  font-size: 18pt;
  padding-left: 25px;
  padding-right: 25px;
}

.testimonials {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.testimonials-container {
  padding-top: 100px;
}

.musician-photo {
  width: 700px;
  height: 650px;
  object-fit: cover;
}

.review-photo {
  width:700px;
  height: 650px;
  object-fit: cover;
}

.history-logo {
  height: 100px;
  width: 100px;
  border-radius: 50%;
}

.first-show {
  background-color: #fff;
  padding-left: 50px;
  
}

.first-show-heading {
  color: #181e2c;
}

.first-show-text {
  color: #9c250f;
  padding-left: 25px;
  padding-right: 25px;
}

.modal-body{
  color: black;
}

.yes{
  color: green;
  font-weight: bold;
}

.no{
  color: red;
  font-weight: bold;
}

@media only screen and (max-width: 400px) {
  .upcoming-shows {
    padding: 0;
  }
  h1 {
    font-size: 24px;
  }
  h2 {
    font-size: 18pt;
  }
  .home-text {
    font-size: 14pt;
  }
  .header-button {
    margin-left: -20%;
  }
  .row {
    margin: 0;
  }
}
